.footer {
  background-color: #cecece;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  -webkit-box-shadow: 0px -1px 4px black;
          box-shadow: 0px -1px 4px black;
}
.footer ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.footer li {
  float: left;
  display: block;
  text-align: center;
  padding: 14px 16px;
}
.footer li a {
  text-decoration: none;
  color: black;
}
#year {
  float: right;
}



